import './BookingSection.css';
import React, { useState, useEffect } from 'react'; 
import { differenceInCalendarDays } from 'date-fns';
import theChangableBase from'../base.js';
import SuccessPopup from './SuccessPopup.js';
import { Calendar,DateRangePicker } from 'react-date-range';
import 'react-date-range/dist/styles.css'; // main style file
import 'react-date-range/dist/theme/default.css'; // theme css file
import DatePicker from 'react-date-picker';
import 'react-date-picker/dist/DatePicker.css';
import 'react-calendar/dist/Calendar.css';
import { format } from 'date-fns';
import InfoSection from '../InfoSection/InfoSection.js';
const BookingSection = () => {
  const [checkIn, setCheckIn] = useState(new Date());
  const [checkOut, setCheckOut] = useState(new Date());
  
  const [numOfGuests, setNumOfGuests] = useState(0);
  const [kidsOfGuests, setKidsOfGuests] = useState(0);
  const [fullName, setFullName] = useState('');
  const [phoneNum, setPhoneNum] = useState('');
  const [email, setEmail] = useState('');
  const numberOfNights = differenceInCalendarDays(new Date(checkOut), new Date(checkIn));
  
  const [minDate,setMinDate]=useState('');
   const [fetchDates, setFetchDates] = useState({});
  const [disabledDates,setDisabledDates]=useState([]);
  const [fetchPricesAndFee, setPricesAndFee] = useState([]);
  const [singlePrice,setSinglePrice]=useState('');
  const [totalPrice,setTotalPrice]=useState('');
  const [fee,setFee]=useState('');
  
  const [showBookingSuccessPopup, setShowBookingSuccessPopup] = useState(false);
 //fetch data from airtable//
 
  useEffect(() => {
    const fetchData = async () => {
    const allRecordsOfDates = [];

    await theChangableBase('Carlendar Notes')
      .select({ view: 'Grid view' })
      .eachPage(
        (records, fetchNextPage) => {
           allRecordsOfDates.push(...records);
          fetchNextPage();
        },
        async (err) => {
	      if (err) {
		console.error('Error fetching records:', err);
	      } else {
		
	         setFetchDates( allRecordsOfDates);
                 console.log("check the fetched dates:",fetchDates);
           }
        }
      );
     const allRecordsOfPricesAndFee = [];
     await theChangableBase('PriceAndFee')
        .select({ view: 'Grid view' })
        .eachPage(
          (records, fetchNextPage) => {
           allRecordsOfPricesAndFee.push(...records);
            fetchNextPage();
          },
          async (err) => {
            if (err) {
              console.error('Error fetching records from Price:', err);
            } else {
             
              const curentPricesAndFee = allRecordsOfPricesAndFee.map(record => ({
                start: record.fields.SeasonStartDate,
                end: record.fields.SeasonEndDate,
                price: record.fields.Price,
                fee: record.fields.CleaningFee,
                season: record.fields.Seasons,
              }));
              setPricesAndFee(curentPricesAndFee);
              console.log("the price and fee are",curentPricesAndFee);
              
             
            }
          }
        ); 
      
   };
  fetchData();
  }, []);

  
 ///////////////////////////////////////////////////
  
  
  
  
  
  const handleStartDateChange = (date) => {
     setCheckIn(date);
    
     const newEndDate = new Date(date);
     newEndDate.setDate(date.getDate() + 5);

     const isOverlap = disabledDates.some((disabledDate) => {
        return disabledDate >= date && disabledDate <= newEndDate;
     });
     if (isOverlap) {
           alert('Selected range overlaps with unavailable dates. Please choose a different range.');
    return;
      }

    
    setMinDate(newEndDate);
    setCheckOut(newEndDate);
  };

  const handleEndDateChange = (date) => {
     const isOverlap = disabledDates.some((disabledDate) => {
        return disabledDate >= checkIn && disabledDate <= date;
     });
     if (isOverlap) {
       alert('Selected range overlaps with unavailable dates. Please choose a different range.');
          return;
      }
     setCheckOut(date);
   };
  
  const isDateInRange = ({ date }) => {
	  if (!checkIn) {
	    return '';
	  }
	  if (!checkOut) {
	   const initialCheckOut = new Date(checkIn);
	   initialCheckOut.setDate(checkIn.getDate() + 5);
	   
	   const isInitialRange = date >= checkIn && date <= initialCheckOut;
	   return isInitialRange  ? 'highlighted-range' : '';
	  }
	  const isNewRange = date >= checkIn && date <= checkOut;
	  return isNewRange  ? 'highlighted-range' : '';
	
  };
  
 
 
 // left part
 const selectionRange = {
      startDate:checkIn, 
      endDate:checkOut,
      key: 'selection',
    }
  const handleSelect = (ranges) => { 
     setCheckIn(ranges.selection.startDate);  
     const newEndDate = new Date(ranges.selection.startDate);
     newEndDate.setDate(ranges.selection.startDate.getDate() + 4);
     setMinDate(newEndDate);                    	
     // automaticly set checkOut date is 4 day more than start dates
     if( newEndDate > ranges.selection.endDate){
        setCheckOut(newEndDate);  
     }else{
       setCheckOut(ranges.selection.endDate);
    } 
    const isOverlap = disabledDates.some((date) => {  
       return date >= checkIn && date <= checkOut;
    });
    if (isOverlap) {
      alert('Selected range overlaps with unavlailbel dates. Please choose a different range.');
      return;
    } 
 }
 
 ///working on disable dates showing on the carlendars
  const timeZone = "Europe/Berlin";
  useEffect(() => {
	  if (Array.isArray(fetchDates)) {
	    // Update this with the correct time zone
	     const updatedDisabledDates = fetchDates.map(record => {
	     const bookedDate = new Date(record.fields["booked"]);
	     const options = { timeZone };
	     return new Date(bookedDate.toLocaleDateString(undefined, options));
	    });

	    setDisabledDates(updatedDisabledDates);
	    //console.log("here is the disable date:",updatedDisabledDates);
	  }
	}, [fetchDates]);
 
  //check disable dates in format M/d/yyyy , then show on it on date picker
  const isDateDisabled = (date) => {
	  const formattedDate = format(date, 'M/d/yyyy', { timeZone });
	  const formattedDisabledDates = disabledDates.map(date => format(date, 'M/d/yyyy'));
	  const isDisabled =formattedDisabledDates.includes(formattedDate);	 
          return isDisabled;
  };
  
  
  ////getselected date
  function getDatesBetween(startDate, endDate) {
	  const datesArray = [];
	  let currentDate = new Date(startDate);
	  while (currentDate <= endDate) {
	    const formattedEachDate = currentDate.toISOString().split('T')[0];
	    console.log("formattedEachDate", formattedEachDate);
	    datesArray.push(formattedEachDate);
	    currentDate.setDate(currentDate.getDate() + 1);
      }


	  return datesArray;
	}
   const datesBetween = getDatesBetween(checkIn, checkOut);
   console.log(datesBetween);
   
   useEffect(() => {
  const calculateTotalCost = (datesBetween, fetchPricesAndFee) => {
    let totalCost = 0;
    let cleaningFee=0;
    if (datesBetween.length === 1) {
      const matchedSeason = fetchPricesAndFee.find((season) => {
        return datesBetween[0] >= season.start && datesBetween[0] <= season.end;
      });

      if (matchedSeason) {
        console.log("Date:", datesBetween[0], "Season price:", matchedSeason.price);
        setSinglePrice(matchedSeason.price);
         setFee(0);
        
        setTotalPrice(0); // Set total cost to 0 for single date
        return;
      }
    }

    const datesWithoutLastDay = datesBetween.slice(0, -1);

    datesWithoutLastDay.forEach((date) => {
      const matchingSeason = fetchPricesAndFee.find((season) => {
        return date >= season.start && date <= season.end;
      });

      if (matchingSeason) {
        console.log("Date:", date, "Season price:", matchingSeason.price);
        totalCost += parseFloat(matchingSeason.price);
        cleaningFee=parseFloat(matchingSeason.fee);
      }
    });

    setTotalPrice(totalCost);
    setFee(cleaningFee);
    console.log("fee is:",cleaningFee);
    setSinglePrice((totalCost / datesWithoutLastDay.length).toFixed(2));
    console.log("Total Cost:", totalCost);
  };

  calculateTotalCost(datesBetween, fetchPricesAndFee);
}, [datesBetween, fetchPricesAndFee]);
  
 
  
  
  
  
  
  
  
  
  
  
  //form submit
  const handleBookingSubmit = async (e) => {
    e.preventDefault();
   
    const formData = {
      "fields": {
      "Start Date": checkIn.toLocaleDateString(),
      "End Date": checkOut.toLocaleDateString(),
      "Number Of Guests": parseInt(numOfGuests),
      "Number Of Kids": parseInt(kidsOfGuests),
      "Full Name": fullName,
      "Email": email,
      "Phone Number": phoneNum,
    }
    };
   
    fetch(`https://api.airtable.com/v0/appPrmFJzdWiFnCpN/ReservationInfo`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': 'Bearer patjcE0Oa54kfWtFE.820c3b5107a690a0160fa2730a0d1a7354c09bdecad6625f180f671b19e2c4e2',
      },
      body: JSON.stringify(formData),
    })
      .then(response => response.json())
      .then(data => {
        console.log(data);
        //alert('Booking form submitted successfully!');
        setShowBookingSuccessPopup(true);
        // You can redirect the user or perform other actions here
      })
      .catch(error => {
        console.error('Error:', error);
        console.log("hi there is the formData",formData); 
        alert('Error submitting the booking form. Please try again.');
      });
  };
  

  
 
 
  
  return (
  
  <div id="booking" className="bookingSection text-lg">
  <div className="bookingContent">
    <div className="leftPart"> 
    <InfoSection />
      <div className="custom-date-range-picker">
      
      <DateRangePicker
			ranges={[selectionRange]}
			onChange={handleSelect}
			disabledDates={disabledDates}
			months={2}
			rangeColors={['#FF5A5F']}
			minDate={ new Date()}
			className="absolute-calendar"/>
       </div>
		
</div>
    < div className="rightPart">
    <form onSubmit={handleBookingSubmit}  className="form">
     <div className="formWidth">
      <div className="rightPart--title">{singlePrice}</div>
      <div style={{width:"100%"}}>
        <div className="rightPart--dateChooseBox" >
           <div className="rightPart--checkInBox">
            <label>Check in:</label>
             <DatePicker 
                       onChange={handleStartDateChange} 
                        value={checkIn}  
                        minDate={new Date()}
                        className="custom-date-picker"  
                        clearIcon={null}
                        tileDisabled={({ date }) => isDateDisabled(date)}

              />
          </div>
          <div className="rightPart--checkOutBox">
            <label>Check out:</label>
            <DatePicker 
          
            onChange={handleEndDateChange} 
            value={checkOut}   
            tileClassName={isDateInRange}
            minDate={minDate} 
            clearIcon={null}
            tileDisabled={({ date }) => isDateDisabled(date)}
           />
          
          </div>
         </div>
       <div className="rightPart--formPadding">
        <div className="form-style">
          <label>Guests:
           <input type="number"
	        value={numOfGuests}
	        id="Number of guests"
	        name="Number of guests"
	        onChange={(e) => setNumOfGuests(e.target.value)}
	        min="1" 
	        required/>
	     </label>
        </div>
          { checkIn && checkOut &&numberOfNights >= 4 &&(
         <div>
         <div className="form-style">
           
	   <label>Children(2-12 year old):
	   <input type="number"
	         value={kidsOfGuests}
	         id="Number of kids"
	         min="0" 
	         name="Number of children"
	         onChange={(e) => setKidsOfGuests(e.target.value)}
	         required/>
	    </label>
        </div>
	<div className="form-style">
	   <label>Full Name:
	   <input type="text"
	         value={fullName}
	         id="fullName"
	         name="Full name"
	         onChange={(e) => setFullName(e.target.value)}
	         required/></label>
	</div>
	 
	 <div className="form-style">
	   <label>Email:
	   <input type="email"
	         name="Email"
	          id="bookingEmail"
	         value={email}
	         onChange={(e) => setEmail(e.target.value)}
	         required/></label>
	 </div>
	 
	 <div className="form-style">
	   <label>Phone Number:
	   <input type="text"
                  id="BookingPhone"
	         name="Phone number"
	         value={phoneNum}
	          onChange={(e) => setPhoneNum(e.target.value)}
	         required/></label>
	 </div>
	</div>)
       }
      </div>
     
       <button className="contactButton btn btn-primary " type="submit" >
            Reserve
        </button>
        
      { checkIn && checkOut &&numberOfNights >= 4&&(
            <div className="forSummary">
            <p>You won't be charged yet</p>
            <div className="summary--section">
              <span> {singlePrice} x {numberOfNights} nights</span>
              <span>{(Number(singlePrice) * Number(numberOfNights)).toFixed(2)}</span>
            </div>
            { numberOfNights > 0 &&(
            <div className="summary--section">
              <span>Cleaning fee</span>
              <span>{fee.toFixed(2)}</span>
            </div>
            )}
            <div className="summary--section totalPrice">
              <span>Total before taxes</span>
               { numberOfNights > 0 ?
              <span>{(totalPrice+fee).toFixed(2)}
</span>:<span>0</span>}
            </div>
          </div>
   )}
  </div>
  </div>
  
     </form>
    </div>
 {showBookingSuccessPopup && numOfGuests && fullName && phoneNum&& email &&(
        <SuccessPopup onClose={() => setShowBookingSuccessPopup(false)}
          numOfGuests={numOfGuests}
          kidsOfGuests={kidsOfGuests}
          fullName={fullName}
          phoneNum={phoneNum}
          email={email}
          checkIn={checkIn.toISOString()} 
          checkOut={checkOut.toISOString()}/>
      )}
    </div>
   
     
  </div>
  );
};

 
export default BookingSection;

         

